"use client";

import Breakcrum from "@/components/Breakcrum";
import Container from "@/components/Container";
import Loading from "@/components/Loading";
import PriceFormatter from "@/components/PriceFormatter";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Order } from "@/constants/types";
import { getOrderSearch } from "@/services/orderService";
import React, { useState } from "react";

type Props = {
  name: string,
  label: string
}

const Lookup = () => {
  const [type, ] = useState<Props[]>([
    {
      name: 'email',
      label: 'Email'
    },
    {
      name: 'phone',
      label: 'Số điện thoại'
    },
  ]);
  const [param, setParam] = useState({
    type: 'email',
    value: ''
  });
  const [data, setData] = useState<Order[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  const hanbleSubmit = async () => {
    try {
      setLoading(true);
      const res = await getOrderSearch(param);
      if(res.data.data) {
        setData(res.data.data);
      } 
    }catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  }

  return (
    <>
        <Breakcrum title="Tra cứu đơn hàng" description="Tra cứu thông tin đơn hàng" />
        <div className="py-5 md:py-10">
            <Container>
                <div className="grid grid-cols-3 gap-10">
                  <div className="col-span-3 md:col-span-1">
                    <div className="p-6 shadow-md rounded-md space-y-4 bg-light_brownish/10">
                      <h3>Kiểm tra đơn hàng của bạn</h3>
                      <div className="flex items-center gap-4">
                        {type.map((item, index: number) => (
                          <div key={index} className="flex items-center gap-2">
                            <Checkbox 
                              id={item.name}
                              checked={param.type === item.name}
                              onCheckedChange={(checked) => {
                                setParam({
                                  ...param,
                                  type: checked ? item.name : ''
                                })
                              }}
                            />
                            <Label htmlFor={item.name} className="text-darkGray text-sm font-normal cursor-pointer flex items-center gap-2">{item.label}</Label>
                          </div>
                        ))}
                      </div>
                      <Input 
                        placeholder="Nhập số điện thoại hoặc email người nhận"
                        className="bg-white"
                        value={param.value}
                        onChange={(e) => {
                          setParam({
                            ...param,
                            value: e.target.value
                          })
                        }}
                      />
                      <div className="flex items-center justify-end">
                        <Button className="h-[30px]" onClick={hanbleSubmit}>Kiểm tra</Button>
                      </div>
                    </div>
                  </div>
                  <div className="col-span-3 md:col-span-2">
                    {loading ? (
                      <>
                        <Loading/>
                      </>
                    ): (
                      <>
                        {data.length > 0 ? (
                          <>
                          {data.map((item, index: number) => (
                            <div key={index} className="p-6 shadow-md rounded-md space-y-4">
                              <div className="grid grid-cols-3 gap-4">
                                <div className="col-span-3">
                                  <span className="text-lg">Mã đơn hàng: 1</span>
                                </div>
                                <div className="col-span-2 flex flex-col text-sm">
                                  <span>Khách hàng: Minh Nghĩa</span>
                                  <span>Số điện thoại: 012345678</span>
                                  <span>Email: nghia.com</span>
                                  <span>Ngày mua: 01/01/2023</span>
                                  <span>Địa chỉ giao hàng: 123, Hà Nội</span>
                                </div>
                                <div className="col-span-1 flex flex-col justify-between text-sm">
                                  <div className="flex flex-col">
                                    <span>Tổng giá sản phẩm: <PriceFormatter amount={1000000}/></span>
                                    <span>Phí vận chuyển: <PriceFormatter amount={1000000}/></span>
                                    <span>Tổng tiền: <PriceFormatter amount={1000000} className="text-base text-red-600"/></span>
                                  </div>
                                  <div>
                                    <span>Số lượng sản phẩm: <span className="text-red-600">2</span></span>
                                  </div>
                                </div>
                                <div className="col-span-3 flex flex-col text-sm">
                                  <span>Trạng thái thanh toán: Đa hóa</span>
                                  <span>Trạng thái đơn hàng: Đa hóa</span>
                                </div>
                              </div>
                            </div>
                          ))}
                          </>
                        ): (
                          <>
                            <div className="flex items-center justify-center p-5">
                              <span className="text-lg text-gray-600">Bạn chưa có đơn hàng nào.</span>
                            </div>
                          </>
                        )}
                      </>
                    )}
                  </div>
                </div>
            </Container>
        </div>
    </>
  );
};

export default Lookup;
